.home-hero {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(130deg, #f3b05a, #f46a4e);
}

.home-hero__wrapper {
  cursor: pointer;
  position: relative;
}

.home-hero__bg-one {
  width: 100%;
  height: 100%;
  background: url("13.603636ca.jpg") center / cover;
  position: absolute;
}

.home-hero__bg-two {
  display: none;
}

.home-hero__header {
  width: 100%;
  background-color: #0000;
  justify-content: center;
  margin: 2em 0;
  display: flex;
  position: fixed;
  top: 0;
}

.header__nav-items {
  flex-direction: row;
  justify-content: space-between;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-items__item {
  color: var(--white);
}

.nav-items__item a {
  color: var(--white);
  margin: 0 8px;
  font-size: 16px;
  font-weight: 500;
}

.home-hero__body {
  min-height: 100vh;
  color: var(--white);
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
  font-family: Work Sans, sans-serif;
  display: flex;
}

.home-hero__body h1 {
  z-index: 20;
  font-size: 40px;
}

.home-hero__body h3 {
  margin-top: 12px;
  font-size: 20px;
  font-weight: 500;
}

.body__quote {
  display: none;
}

.home-hero__footer {
  color: var(--white);
  height: 50px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2em 0;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.home-hero__footer span {
  font-size: 14px;
}

@media (hover: hover) {
  .nav-items__item a:hover {
    color: var(--darkOrange);
    font-weight: 700;
  }
}

@media (min-width: 768px) {
  .home-hero__bg-two {
    width: 100%;
    height: 100%;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 0, 0% 100%);
    clip-path: polygon(0 0, 100% 0, 100% 0, 0% 100%);
    background: url("8.47a368c4.png") center / cover;
    transition: all .6s;
    display: block;
    position: absolute;
  }

  .home-hero__bg-two:hover {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }

  .home-hero__bg-one:hover ~ .home-hero__bg-two {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }

  .nav-items__item a {
    font-size: 18px;
  }

  .home-hero__body {
    align-items: center;
  }

  .home-hero__body h1 {
    font-size: 85px;
  }

  .home-hero__body h3 {
    font-size: 40px;
  }

  .body__quote {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 4em;
    display: flex;
  }

  .quote__text {
    padding: .35em 2.5em;
    font-size: 18px;
    font-style: italic;
    line-height: 1.45;
    position: relative;
  }

  .quote__text:before {
    content: "“";
    padding-left: 8px;
    font-size: 90px;
    display: block;
    position: absolute;
    top: -20px;
    left: -20px;
  }

  .quote__cite {
    margin-top: 5px;
    font-size: 14px;
    display: block;
  }

  .quote__cite:before {
    content: "— ";
  }

  .home-hero__footer {
    flex-direction: row;
  }
}

